.CardBody {
  padding: 0;
}

.ContainerWhole {
  width: 100%;
  border: 0px !important;
  border-radius: 0px !important;
}

.CardHeader {
  background: rgba(196, 196, 196, 0.3);
  font-family: Nunito Sans;
  font-style: normal;
  min-height: 54px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-weight: bold;
  font-size: 15px;
  line-height: 20px;
  color: #000000;
  padding-left: 24px;
  border-bottom: 0px;
  border-radius: 0px !important;
}

.HeaderInside {
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 13px;
  line-height: 18px;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 19px;
  padding-bottom: 19px;
  color: #444444;
  margin: 0px;
}

.Content {
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.Content img {
  padding-left: 10px;
}

.FlagText {
  padding-top: 10px;
  padding-bottom: 20px;
}

.CardBodyContent {
  padding: 0px 24px 0px 24px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #f7f7f7;
}

.LeftBody {
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 18px;
  /* identical to box height */
  color: #696969;
}

.RowContainer {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0px;
  padding-bottom: 16px;
}

.RightBody {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: right;
  font-weight: bold;
  font-size: 13px;
  color: #000000;
}

.TransListIcon {
  margin-left: 0.5rem;
}

.CardContainer {
  border-right: none !important;
  border-left: none !important;
}
